:root {
  --base-font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;

  --base-color-primary-hue: 250;
  --base-color-primary: hsl(var(--base-color-primary-hue), 100%, 60%);
  --base-color-primary-hover: hsl(var(--base-color-primary-hue), 100%, 50%);

  --base-color-secondary-hue: 250;
  --base-color-secondary: hsl(var(--base-color-secondary-hue), 100%, 95%);
  --base-color-secondary-hover: hsl(var(--base-color-secondary-hue), 100%, 90%);

  --base-color-success-hue: 140;
  --base-color-success: hsl(var(--base-color-success-hue), 100%, 40%);
  --base-color-success-hover: hsl(var(--base-color-success-hue), 100%, 30%);

  --base-color-danger-hue: 0;
  --base-color-danger: hsl(var(--base-color-danger-hue), 100%, 40%);
  --base-color-danger-hover: hsl(var(--base-color-danger-hue), 100%, 30%);

  --base-color-font: #666;
  --base-color-font-light: #888;
  --base-color-font-dark: #333;

  --base-color-focus: #1f00ff;
  --base-color-focus-light: #d2daff;
  --base-color-focus-dark: darkblue;

  --base-color-white: white;
  --base-color-black: black;

  --base-color-ui-lightest: #f5f5f5;
  --base-color-ui-lighter: #eee;
  --base-color-ui-light: #cbcbcb;
  --base-color-ui: #9a9a9a;
  --base-color-ui-dark: #5e5e5e;
  --base-color-ui-darker: #565656;
  --base-color-ui-darkest: #333;

  --base-container: 800px;
  --base-container-sm: 650px;
  --base-container-md: 800px;
  --base-container-lg: 1200px;
  --base-container-xl: 1400px;

  --base-space-xs: 4px;
  --base-space-sm: 8px;
  --base-space-md: 16px;
  --base-space-lg: 32px;
  --base-space-xl: 64px;

  --base-font-size-xs: 0.83em;
  --base-font-size-sm: 1em;
  --base-font-size-md: 1.17em;
  --base-font-size-lg: 1.5em;
  --base-font-size-xl: 2em;

  --base-border-radius-xs: 2px;
  --base-border-radius-sm: 4px;
  --base-border-radius-md: 8px;
  --base-border-radius-lg: 16px;
  --base-border-radius-xl: 32px;

  --base-size: 48px;
  --base-size-xs: 24px;
  --base-size-md: 48px;
  --base-size-sm: 36px;
  --base-size-lg: 58px;

  /* DEPTH */
  --base-depth-none: none;
  --base-depth-xs: 0px 1px 1px 0px rgba(9, 30, 66, 0.06),
    0px 2px 4px 0px rgba(9, 30, 66, 0.15);
  --base-depth-sm: 0px 2px 2px 0px rgba(9, 30, 66, 0.06),
    0px 4px 8px -2px rgba(9, 30, 66, 0.25);
  --base-depth-md: 0px 2px 2px 0px rgba(9, 30, 66, 0.06),
    0px 8px 16px -4px rgba(9, 30, 66, 0.25);
  --base-depth-lg: 0px 4px 4px 0px rgba(9, 30, 66, 0.06),
    0px 12px 24px -6px rgba(9, 30, 66, 0.25);
  --base-depth-xl: 0px 6px 6px 0px rgba(9, 30, 66, 0.1),
    0px 20px 32px -8px rgba(9, 30, 66, 0.25);
}

[theme="minimal"] {
  font-family: var(--base-font-family);
}
